import angular from 'angular'

export default angular
  .module('MobileApInfopControllerModule', [])
  .controller('MobileAppInfoController', function (
    $scope,
    $state,
    analytics,
    auth,
    canvas,
    /* ClientConfig */ config,
    mobileService
  ) {
    $scope.mobileAppVersion =
      mobileService.appVersion + (mobileService.buildNumber ? `.${mobileService.buildNumber}` : '')
    $scope.webAppVersion = config.version
    $scope.dependencies = config.timestamp
    $scope.configs = config.$enterpriseConfigVersion
    $scope.codepush = mobileService.codePushBundleLabel || ''

    const showExtendedInfo = config.env.mode !== 'prod' || !!$state?.params?.show_app_extended_info
    if (showExtendedInfo) {
      $scope.userID = auth.user?.id ?? ''
      $scope.UAchannel = mobileService.UrbanAirshipChannelId || ''
    }

    $scope.logMobileAppView = () => {
      canvas.logPageView('mobileappview')
      analytics.logSegmentEvent('store.mobileapp_view')
    }
  })
